<template>
  <div></div>
</template>
<script>
import firebase from 'firebase/app'
import 'firebase/app'
import 'firebase/messaging'
import axios from "axios";
export default {
  data() {
    return {
      fcmdata_ :{
        user : localStorage.getItem('loggedUser'),
        user_id: '',
        fcmtoken:''

      }
    }
  },
  mounted() {
    
    var config = {
      apiKey: 'AIzaSyBszkKAUdRj2VMG4UYwXifSrFgsZjlvfLg',
      authDomain: 'ashiksam-247.firebaseapp.com',
      projectId: 'ashiksam-247',
      storageBucket: 'ashiksam-247.appspot.com',
      messagingSenderId: '422084835205',
      appId: '1:422084835205:web:c6cca3c4e25f3a3f40b6ec',
      measurementId: 'G-RMSV7G55TT',
    }
    if (!firebase.apps.length) {
        firebase.initializeApp(config)
     }
    

    const messaging = firebase.messaging()

    messaging.usePublicVapidKey(
      'BJZXBLzqC4U42_YG1v-w8y1pTqpCbt5VpfRCOX8Y8EiWH-mnHZmvFuzSbECmjoNGsi420yB-3rqEQnWvmMa6TP4',
    )

    messaging
      .requestPermission()
      .then(() => {
        console.log('Notification permission granted.')
        messaging.getToken().then(token => {
          console.log('New token created: ', token)
          this.fcmdata_.fcmtoken = token
        
          if( localStorage.getItem('token') == null)
          {
            this.saveNotificationToken()
          }
          localStorage.setItem('token', token)
          // this.saveNotificationToken(token)
        })
      })
      .catch(err => {
        console.log('Unable to get permission to notify.', err)
      })

    messaging.onTokenRefresh(function () {
      messaging
        .getToken()
        .then(function (newToken) {
          console.log('Token refreshed: ', newToken)
          this.fcmdata_.fcmtoken = newToken
          this.saveNotificationToken()

          localStorage.setItem('token', token)
        })
        .catch(function (err) {
          console.log('Unable to retrieve refreshed token ', err)
        })
    })

    this.fcmdata_.user_id =  JSON.parse(localStorage.getItem('userinfo'))._id;
  },
  methods:{
    saveNotificationToken()
    {
      let user_log = localStorage.getItem('loggedUser');
      if (user_log !== 'undefined' && user_log !== null)
      {
        let apiURL = `api/postAPI/save-token`;
          axios.post(apiURL, this.fcmdata_).then((res) => {
          if(res.status == 200)
          {
            console.log("token saved"+res)
          }else
          {
              console.log(res)
          }
          
        }).catch(error => {
          console.log(error)
        })
      }
       
    }
  }
}
</script>